<template>
	<div class="player">
		<div ref="play" class="play"></div>
	</div>
</template>

<script>
	import 'DPlayer/dist/DPlayer.min.css';
	
//	import Hls from "hls";
//	import "../../static/js/dash.js";
//	import "../../static/js/hls.min.js";
//	import webtorrent from "webtorrent";  
	import DPlayer from 'DPlayer';
	import video from "../../static/js/video.json";
	export default {
		props: ["animateID","videoIndex"],
		data(){
			return {
				videoList: {},
				dp: null,
				videoUrl: '',
				isVideo: false,
				options: {
				    container: '',
				    autoplay: false,
				    video: {
				    	url: 'blob:http://player.jfrft.net/c7003d66-441e-4952-a0a6-458809543357',
				        type: 'hls',
				         customType: {
				            'customHls': function (video, player) {
				            	if(video.src!=""){
			            			const hls = new Hls();
					                hls.loadSource(video.src);
					                hls.attachMedia(video);
				            	}
				                
				            }
				        }
				    }
				}
			}
		},
		watch: {
			videoIndex(newVal){
				if(this.isVideo){
					this.selectVideo();
				}
			},
			videoUrl(newVal,oldVal){
				console.log(newVal)
				if(newVal != "" && newVal != oldVal){
					this.dp.pause();
					this.dp.switchVideo({
					    url: newVal,
					     type: 'hls',
//				         customType: {
//				            'customHls': function (video, player) {
//				            	if(video.src!=""){
//			            			const hls = new Hls();
//					                hls.loadSource(video.src);
//					                hls.attachMedia(video);
//				            	}
//				                
//				            }
//				        }
					});
					this.dp.play();
				}
			}
		},
		mounted(){
			this.videoList = video.videoJSON;
			this.newPlay();
			this.selectVideo();
		},
		methods:{
			selectVideo(){
				console.log(2)
				for(let i = 0; i< this.videoList.length; i++){
					if(this.videoList[i].animate == this.animateID){
						for(let video in this.videoList[i].list){
							if(video == this.videoIndex){
								this.videoUrl = this.videoList[i].list[video];
//								this.options.video.url = this.videoUrl;
							}
						}
					}
				};
			},
			newPlay(){
				console.log(1)
				this.options.container = this.$refs.play;
				this.dp = new DPlayer(this.options);
				this.isVideo = true;
			}
		}
	}
</script>

<style lang="less">
	.player{
		.play{
			//height: 4.6rem;
		}
	}
</style>


